export const lastDayCreator = (
  type,
  mastoiditisSide,
  mastoditisOperationType,
  age
) => {
  const adenoidsStr = type === `adenoids` ? `   .` : ``;
  const otoscopyNormal = ` AD=AS    ,   -,    .`;
  const beginning = `:   -, ,   . ${adenoidsStr}       , .          . ${otoscopyNormal}`;

  const finishing = ` .     . `;
  const defaultTemplate = `${beginning}${finishing}`;

  const skinWoundTemplate = ` ${beginning}  ,   . ${finishing}`;
  const abscessTemplate = `${beginning}  '.
    ,   ,     . ${finishing}`;
  const mastoiditisShunting =
    mastoditisOperationType === `shunting`
      ? `${
          mastoiditisSide === ""
            ? ` . `
            : ` .`
        }`
      : ``;
  const mastoiditisTemplate = ` ${beginning} ${mastoiditisShunting} ${finishing}`;
  const hearingLossTemplate = `${
    age > 4
      ? "        ."
      : ""
  } ${beginning} ${finishing}`;
  switch (type) {
    case "skinWound":
      return skinWoundTemplate;
    case "abscess":
      return abscessTemplate;
    case "neoplasm":
      return skinWoundTemplate;
    case "mastoiditis":
      return mastoiditisTemplate;
    case "hearingLoss":
      return hearingLossTemplate;
    default:
      return defaultTemplate;
  }
};
