export const operationContentCreator = (
  type,
  sutureType,
  sutureMaterial,
  sutureSize,
  anestesiaType,
  abscessType,
  abscessArea,
  abscessSide,
  abscessReason,
  coblation,
  acuteOtitisSide,
  tampon,
  septumCurvatureSide,
  otitisTypeOperation,
  mastoiditisSide,
  mastoditisOperationType,
  paratonsilarAbscessSide
) => {
  const opBeginning = `    .`;
  const finishing = `   .        .`;
  const anestesiaTypeMod = `${anestesiaType} .`;
  const adenoidsStr = coblation
    ? `          .`
    : ` 3        .`;
  const adenoidsTemplate = ` ${anestesiaTypeMod} ${opBeginning} ${adenoidsStr}  ,  .   . ${finishing}`;
  const skinWoundTemplate = `${anestesiaTypeMod} ${opBeginning}  .       (3%).      ${sutureType},   '${sutureMaterial}${sutureSize}'.    .   '. ${finishing}`;
  const neoplasmTemplate = ` ${opBeginning}         ;  .    .     . ${finishing}`;

  const triangle = ` ${abscessArea.slice(0, -2)} `;

  const paralelMark = `  `;

  const cutLength =
    abscessType === `` ? ` 1 ` : ` 2 `;
  const cutArea = `  ${triangle} ${abscessSide} ${paralelMark}, ${cutLength}.`;
  const cutLayers = `     .`;
  const volume =
    abscessType === "" ||
    abscessType === "" ||
    abscessType === ""
      ? "2"
      : "5";
  const nodeExtraction =
    abscessReason === ""
      ? "           ."
      : abscessType === ""
      ? "-    ."
      : ``;
  const ablution = `    `;
  const abscessTemplate = `${anestesiaTypeMod} ${opBeginning} ${cutArea} ${cutLayers} 
            .
     ' ${volume} .    .. ${nodeExtraction}
  ${ablution}    .  '     . ${finishing}`;
  const paracentesis =
    acuteOtitisSide === "" || mastoiditisSide === ""
      ?  `    ,         ,  . 
          ,         ,  . `
      : `    ${acuteOtitisSide},         ,  . `;
  const shunting =
    acuteOtitisSide === "" || mastoiditisSide === ""
      ?   "        - .   ,         ,      .          .    ."
      : `      ${acuteOtitisSide}${mastoiditisSide}  - .   ,      ${acuteOtitisSide}${mastoiditisSide}   ,      .     ${acuteOtitisSide}     .`;
  const acuteOtitisTemplate = `${anestesiaTypeMod} ${opBeginning}
 ${otitisTypeOperation === "paracentesis" ? paracentesis : shunting}
 ${finishing}`;
  const tamponadeType = tampon
    ? ` `
    : `    `;
  const noseFractureTemplate = `${anestesiaTypeMod} ${opBeginning} 
        . 
  .       .  ,  . 
            ${tamponadeType}. 
  .    . ${finishing}`;
  const septumSide = septumCurvatureSide ? `` : "";
  const septumCurvatureTemplate = `
             ${septumSide},
     0,5  .
           . 
          . 
        .
          .
        . ${finishing}
`;
  const mastoidEctomia = `${anestesiaTypeMod}  -   i 2% 2.0   0,1% 0,2  .
  ${opBeginning}         ${
    mastoiditisSide === "" ? "" : mastoiditisSide
  }
  .        ,
      - ,  .
          .
       .      .
            .
               .
            ,      .   .
         ${
           mastoiditisSide === ""
             ? "    ."
             : ""
         } `;
  const mastoiditisPart =
    mastoditisOperationType === "paracentesis"
      ? paracentesis
      : mastoditisOperationType === "shunting"
      ? shunting
      : mastoditisOperationType === "mastoidEctomia"
      ? mastoidEctomia
      : `${mastoidEctomia} ${shunting} `;
  const mastoiditisTemplate = `${anestesiaTypeMod} ${opBeginning} ${mastoiditisPart} ${finishing}`;
  const tonsilitisTemplate = `${anestesiaTypeMod}  i      . 
   i i        i        .
    :  .     .    . 
   ${finishing}`;
  const paratonsilarAbscessTemplate = `
   ${anestesiaTypeMod} 
       .
         ${paratonsilarAbscessSide}
    1,0  1 %   .
     " "        
       ,    . . ${finishing} `;
  switch (type) {
    case "adenoids":
      return adenoidsTemplate;
    case "skinWound":
      return skinWoundTemplate;
    case "abscess":
      return abscessTemplate;
    case "neoplasm":
      return neoplasmTemplate;
    case "acuteOtitis":
      return acuteOtitisTemplate;
    case "mastoiditis":
      return mastoiditisTemplate;
    case "noseFracture":
      return noseFractureTemplate;
    case "septumCurvature":
      return septumCurvatureTemplate;
    case "tonsilitis":
      return tonsilitisTemplate;
    case "paratonsilarAbscess":
      return paratonsilarAbscessTemplate;
    default:
      return "";
  }
};
