import React from "react";
import "./styles.css";
import { Block } from "./block";

export const Diary = ({
  current,
  firstIndex,
  lastIndex,
  patient, 
  params
}) => {
  const { mutualExamination } = params;
  const hrCounter = (rate) => {
    let numRate = Number(rate);
    let randomInc = Number((Math.random() * 3).toFixed(0));
    numRate = numRate + randomInc;
    return numRate;
  };
  const rrCounter = (rate) => {
    let numRate = Number(rate);
    let randomInc = Number((Math.random() * 5).toFixed(0));
    numRate = numRate + randomInc;
    return numRate;
  };
  const {heartRate, respiratoryRate, lastDayContent, disease,
    someDayContent, doctor
  } = patient;
  return (
    <div id="diary">
      <div className="flexi flexDiary">
        <Block header=":" content={current.date} />
      </div>
      {current.workDay && (
        <div id="workdayBlock">
          <div className="flexi flexDiaryMiddle">
            <Block
              header={mutualExamination}
              content=""
            />
          </div>
          <div className="flexi flexDiaryMiddle">
            <Block
              header=""
              content={`=${hrCounter(heartRate)}/; =${rrCounter(
                respiratoryRate
              )}/; t - N`}
            />
          </div>

          {lastIndex && (
            <div className="diary">
              <Block
                header=""
                content="   .  .  ,   .   ,  , .    .  .    , .   .     .    ."
              />
            </div>
          )}
          {!lastIndex && (
            <div className="diary">
              <Block
                header=""
                content="     .  ,   .   ,  , .    .  .    , .   .     .    ."
              />
            </div>
          )}

          {lastIndex && (
            <div className="diary">
              <Block header="" content={lastDayContent} />
            </div>
          )}
          {!lastIndex && disease !== "septumCurvature" && (
            <div className="diary">
              <Block header="" content={someDayContent} />
            </div>
          )}
          {!lastIndex && !firstIndex && disease === "septumCurvature" && (
            <div className="diary">
              <Block header="" content={someDayContent} />
            </div>
          )}
          {firstIndex && disease === "septumCurvature" && (
            <div className="diary">
              <Block
                header=""
                content=" .      .  . 
:  -, ,   ,   . 
    .
"
              />
            </div>
          )}
          <div className="flexEnd headers">
            <div className="lastLine">
              <Block header="" content={` ________ ${doctor}`} />
            </div>
          </div>
        </div>
      )}
      {!current.workDay && (
        <div className="flexi">
          <Block
            header=""
            content=" .       ."
          />
        </div>
      )}
    </div>
  );
};
