export const additionStatusCreator = (
  type,
  woundSide,
  woundArea,
  woundType,
  woundLength,
  woundWidth,
  woundDepth,
  woundForm,
  woundLedges,
  woundBleeding,
  woundAliens,
  abscessType,
  abscessArea,
  abscessSide,
  neoplasmArea,
  neoplasmSide,
  neoplasmForm,
  neoplasmBorders,
  neoplasmMovability,
  neoplasmPoignancy,
  neoplasmConsistance,
  neoplasmDiameter,
  neoplasmSurface,
  neoplasmColor,
  neoplasmAboveSurface,
  neoplasmSurfaceAlter,
  sinusitisType,
  sinusitisSide,
  sinusitisCT
) => {
  const neoplasmAbove = neoplasmAboveSurface ? `` : ``;
  const colorAlter = neoplasmSurfaceAlter ? `` : ``;
  const neoplasmTemplate = `  ${neoplasmArea} ${neoplasmSide}, ${neoplasmForm} , ${neoplasmColor} , ${neoplasmBorders},  ${neoplasmAbove}   .  : ${neoplasmMovability}, ${neoplasmPoignancy}, ${neoplasmConsistance} .   ${neoplasmDiameter} ; ${neoplasmSurface}     ${colorAlter} .`;

  const abscessAreaMod =
    abscessArea === "   "
      ? abscessArea
      : abscessArea[abscessArea.length - 1] === ""
      ? `${abscessArea}`
      : `${abscessArea} `;
  const abscessTemplate =
    abscessType === ""
      ? `       '  ${abscessAreaMod} ${abscessSide}.
   , ,    . 
     ,     . `
      : abscessType === ``
      ? `         ${abscessAreaMod} ${abscessSide}.
       ,    ,   ,    . 
          - . `
      : `        ${abscessAreaMod} ${abscessSide}.
    , . 
   , ,     . `;

  let woundSize =
    woundDepth !== "" && woundWidth !== "" && woundLength !== ""
      ? `   ${woundLength}*${woundWidth}*${woundDepth} `
      : "";

  const skinWoundTemplate = `   ${woundArea}${woundSide}  ${woundType}  ${woundForm} ,${woundSize}. ${woundLedges}.    ${woundBleeding}. ${woundAliens}.`;
  const preSinusitisTemplate =
    sinusitisType === `maxillaSinusitis`
      ? `    , 
            ${
        sinusitisSide !== ``
          ? `   ${sinusitisSide}`
          : `  `
      }.`
      : sinusitisType === `etmoiditis`
      ? `    , 
             ${
        sinusitisSide !== ``
          ? `  ${sinusitisSide}`
          : `  `
      }.`
      : sinusitisType === `frontitis`
      ? `    , 
            ${
        sinusitisSide !== ``
          ? `  ${sinusitisSide}`
          : `  `
      }.`
      : sinusitisType === `hemisinusitis`
      ? `    , 
             , , ,    ${sinusitisSide}.`
      : sinusitisType === `pansinusitis`
      ? `    , 
             , , ,    .`
      : `  .`;
  const sinusitisTemplate = sinusitisCT ? preSinusitisTemplate : "";

  switch (type) {
    case "skinWound":
      return skinWoundTemplate;
    case "abscess":
      return abscessTemplate;
    case "neoplasm":
      return neoplasmTemplate;
    case "sinusitis":
      return sinusitisTemplate;
    default:
      return ``;
  }
};

