export const noseCreator = (type, noseFractureSide, sinusitisType) => {
  const noseBreathing =
    type === "adenoids" || type === "septumCurvature" ? `` : ``;
  const defaultTempate = `         .        ,     .
     ${noseBreathing}   .         . `;
  const noseFractureTemplate = ` i i     ${noseFractureSide}   .
             .
         .  `;
  const sinusitisTemplate = `         .        i,     .
       ,    ,  . `;
  switch (type) {
    case "noseFracture":
      return noseFractureTemplate;
    case "sinusitis":
      return sinusitisTemplate;
    default:
      return defaultTempate;
  }
};

export const rhinoscopyCreator = (
  type,
  noseFractureSide,
  septumCurvatureSide,
  sinusitisType,
  sinusitisSide,
  sinusitisVariety
) => {
  const addition =
    type === "adenoids"
      ? `   III- .`
      : ``;
  const septumSide = septumCurvatureSide ? `` : "";
  const defaultTempate = `  ,   ,   -,   ,      ii,   . ${addition}`;
  const noseFractureTemplate = `    ,     ${noseFractureSide},       i,
    .    ii,    - . `;
  const septumCurvatureTemplate = `  ,     ${septumSide},   -,   ,       ,   .`;
  const sinusitisTemplate = `  ,   , 
    , ,  ${
    sinusitisType === `maxillaSinusitis`
      ? "  "
      : "     "
  } ${sinusitisSide !== `` ? sinusitisSide : "  "} ${
    sinusitisVariety === ``
      ? "-  - "
      : "   "
  },      ii.  . `;
  switch (type) {
    case "noseFracture":
      return noseFractureTemplate;
    case "septumCurvature":
      return septumCurvatureTemplate;
    case "sinusitis":
      return sinusitisTemplate;
    default:
      return defaultTempate;
  }
};

