export const laryngoscopyCreator = (type) => {
  const defaultTempate = `  ,  ,    .        ,    , i      ,    ,   -ii,       ,        , i   ;  ,  .`;
  return defaultTempate;
};
export const otoscopyCreator = (
  type,
  acuteOtitisSide,
  mastoiditisSide,
  acuteOtitisType
) => {
  const bothSides = "  ";
  const firstDefaultPart = `   .     -.   .
     .`;
  const defaultTempate = ` ${firstDefaultPart}   -,    .  .  .
  `;
  const drumPart =
    acuteOtitisSide === ""
      ? `     , - .`
      : `  ${
          acuteOtitisSide !== "" ? acuteOtitisSide : bothSides
        } , - .`;
  const recognizePart = `    ${
    acuteOtitisSide !== "" ? acuteOtitisSide : bothSides
  }.`;
  const cataralDrumPart = `     ${
    acuteOtitisSide !== "" ? acuteOtitisSide : bothSides
  }. `;
  const purulentTemplate = `${firstDefaultPart} ${drumPart} ${recognizePart}`;
  const cataralTemplate = `${firstDefaultPart} ${cataralDrumPart} ${recognizePart}`;
  const acuteOtitisTemplate =
    acuteOtitisType === "" ? purulentTemplate : cataralTemplate;
  const healthySide = mastoiditisSide === `` ? "" : "";
  const mastoiditisMiddlePart =
    mastoiditisSide === ``
      ? `      ${bothSides}.      ${bothSides}.
   . 
 .  .   ${bothSides} ,
   .`
      : `C : ${healthySide}  , ${mastoiditisSide} 
                . 
         ${healthySide} .   ${healthySide} . 
             ${healthySide}. 
       .  .     ${mastoiditisSide},   , 
        ,    .  .
      `;
  const mastoiditisTemplate = `   
          ${
    mastoiditisSide !== "" ? mastoiditisSide : bothSides
  }. 
  ${mastoiditisMiddlePart}
  `;
  switch (type) {
    case "acuteOtitis":
      return acuteOtitisTemplate;
    case "mastoiditis":
      return mastoiditisTemplate;
    default:
      return defaultTempate;
  }
};
