export const operationNameCreator = (
  type,
  woundArea,
  woundSide,
  abscessType,
  abscessArea,
  abscessSide,
  neoplasmArea,
  neoplasmSide,
  acuteOtitisSide,
  otitisTypeOperation,
  mastoiditisSide,
  mastoditisOperationType,
  acuteOtitisType,
  paratonsilarAbscessSide
) => {
  const adenoidsTemplate = `.`;
  const neoplasmTemplate = `   ${neoplasmArea} ${neoplasmSide}.`;
  const aType =
    abscessType === `` ||
    abscessType === `` ||
    abscessType === ``
      ? ``
      : `${abscessType.slice(0, -1)}`;
  const aArea = `${abscessArea} `;
  const abscessTemplate = `   ${aType} ${aArea} ${abscessSide}.`;
  const skinWoundTemplate = `  ${woundArea}${woundSide}.`;
  const sinusitisTemplate =
    "  0.9%   , 5   .";
  const shunting =
    acuteOtitisSide === "" || mastoiditisSide === ""
      ? `   .`
      : `   ${acuteOtitisSide}${mastoiditisSide}.`;
  const paracentesis =
    acuteOtitisSide === "" || mastoiditisSide === ""
      ? `  .`
      : `   ${acuteOtitisSide}${mastoiditisSide}.`;
  const mastoiditisTemplate =
    mastoditisOperationType === "paracentesis"
      ? paracentesis
      : mastoditisOperationType === "shunting"
      ? shunting
      : mastoditisOperationType === "mastoidEctomia"
      ? `${
          mastoiditisSide === ``
            ? ` `
            : ` ${mastoiditisSide}`
        }.`
      : `${
          mastoiditisSide === ``
            ? ` `
            : ` ${mastoiditisSide}`
        }  ${shunting} `;

  const acuteOtitisTemplate =
    acuteOtitisType === ""
      ? sinusitisTemplate
      : otitisTypeOperation === "paracentesis"
      ? paracentesis
      : shunting;

  const noseFractureTemplate = `  .`;
  const septumCurvatureTemplate = `   .`;
  const tonsilitisTemplate = `.`;
  const paratonsilarAbscessTemplate = `   ${paratonsilarAbscessSide}.`;
  const hearingLossTemplate = `    .`;
  switch (type) {
    case "adenoids":
      return adenoidsTemplate;
    case "skinWound":
      return skinWoundTemplate;
    case "abscess":
      return abscessTemplate;
    case "neoplasm":
      return neoplasmTemplate;
    case "acuteOtitis":
      return acuteOtitisTemplate;
    case "mastoiditis":
      return mastoiditisTemplate;
    case "noseFracture":
      return noseFractureTemplate;
    case "septumCurvature":
      return septumCurvatureTemplate;
    case "sinusitis":
      return sinusitisTemplate;
    case "tonsilitis":
      return tonsilitisTemplate;
    case "paratonsilarAbscess":
      return paratonsilarAbscessTemplate;
    case "hearingLoss":
      return hearingLossTemplate;
    default:
      return "";
  }
};
