export const anamnesisMorbiCreator = (
  type,
  start1,
  start2,
  injuryDate,
  injuryTime,
  injuryReason,
  abscessType,
  abscessReason,
  growthRate,
  sinusitisType,
  sinusitisVariety
) => {
  const adenoidsTemplate = `  ,      .`;
  const neoplasmTemplate = `  ,   ,      ${growthRate}.`;
  let _injuryTime = injuryTime === "" ? "" : ` ${injuryTime} `;
  const skinWoundTemplate = `  ,   ${injuryDate} ${_injuryTime},   ${injuryReason}.    ,  .`;
  const beginning = `  ,  ${start1}- ${start2}.`;
  const abscessFinishing =
    abscessReason === ""
      ? " '      ."
      : abscessType === ""
      ? ` '    "".`
      : "     '.";
  const acuteOtitisTemplate = `${beginning}  ,  .`;
  const septumCurvatureTemplate = `  .`;
  const sinusitisTemplate =
    (sinusitisType === "maxillaSinusitis" || sinusitisType === "etmoiditis") &&
    sinusitisVariety === ""
      ? `${beginning}        .`
      : beginning;
  const hearingLossTemplate =
    start1 === 1 && start2 === "" ? "  ." : beginning;
  switch (type) {
    case "adenoids":
    case "tonsilitis":
      return adenoidsTemplate;
    case "skinWound":
      return skinWoundTemplate;
    case "abscess":
      return `${beginning} ${abscessFinishing}`;
    case "neoplasm":
      return neoplasmTemplate;
    case "acuteOtitis":
    case "mastoiditis":
      return acuteOtitisTemplate;
    case "noseFracture":
      return skinWoundTemplate;
    case "septumCurvature":
      return septumCurvatureTemplate;
    case "sinusitis":
      return `${sinusitisTemplate}`;
    case "paratonsilarAbscess":
      return beginning;
    case "hearingLoss":
      return hearingLossTemplate;
    default:
      return "  .";
  }
};
