export const diagnosisCreator = (
  type,
  woundType,
  woundArea,
  woundSide,
  abscessType,
  abscessArea,
  abscessSide,
  abscessReason,
  neoplasmType,
  neoplasmArea,
  neoplasmSide,
  acuteOtitisSide,
  mastoiditisSide,
  isOtitisComplication,
  sinusitisType,
  sinusitisSide,
  sinusitisVariety,
  acuteOtitisType,
  paratonsilarAbscessSide,
  hearingLossType,
  hearingLossSide,
  hearingLossDegree
) => {
  const neoplasmTemplate = `${neoplasmType} ${neoplasmArea} ${neoplasmSide}.`;
  const adenoidsTemplate = `  .`;
  const aReason = abscessReason === "" ? `` : ``;

  const abscessTemplate =
    abscessReason === "" && abscessType === ""
      ? `   ${abscessArea} ${abscessSide}.`
      : abscessType === ""
      ? `  ${abscessArea} ${abscessSide}.`
      : `${aReason}${abscessType} ${abscessArea} ${abscessSide}.`;
  const skinWoundTemplate = `${woundType}  ${woundArea} ${woundSide}.`;

  const aoSide = sideConverter(acuteOtitisSide);
  const mastSide = sideConverter(mastoiditisSide);
  const acuteOtitisTemplate = ` ${acuteOtitisType} ${aoSide}  .`;
  const noseFractureTemplate = `     .`;
  const septumCurvatureTemplate = `      . `;
  const mastoiditisTemplate = isOtitisComplication
    ? ` ${mastSide}    .`
    : `  ${mastSide} .`;
  const sinusitisTemplate = ` ${sinusitisVariety} ${sideConverter(
    sinusitisSide
  )} ${
    sinusitisType === `maxillaSinusitis`
      ? ` `
      : sinusitisType === `etmoiditis`
      ? ``
      : sinusitisType === `frontitis`
      ? ``
      : sinusitisType === `hemisinusitis`
      ? ``
      : sinusitisType === `pansinusitis`
      ? ``
      : ``
  }.`;
  const tonsilitisTemplate = `  .`;
  const sideSplittedArray = paratonsilarAbscessSide
    ? paratonsilarAbscessSide.split("")
    : [""];
  const paratonsilarAbscessTemplate = `${sideSplittedArray[0]}  `.slice(
    1
  );
  const hearingLossTemplate = `${hearingLossType} ${hearingLossSide}   
  ${hearingLossDegree}- .`;
  switch (type) {
    case "adenoids":
      return adenoidsTemplate;
    case "skinWound":
      return skinWoundTemplate;
    case "abscess":
      return abscessTemplate;
    case "neoplasm":
      return neoplasmTemplate;
    case "acuteOtitis":
      return acuteOtitisTemplate;
    case "mastoiditis":
      return mastoiditisTemplate;
    case "noseFracture":
      return noseFractureTemplate;
    case "sinusitis":
      return sinusitisTemplate;
    case "tonsilitis":
      return tonsilitisTemplate;
    case "septumCurvature":
      return septumCurvatureTemplate;
    case "paratonsilarAbscess":
      return paratonsilarAbscessTemplate;
    case "hearingLoss":
      return hearingLossTemplate;
    default:
      return "";
  }
};
const sideConverter = (side) => {
  const result =
    side === ""
      ? side
      : side === ""
      ? ""
      : side === ""
      ? ""
      : "";
  return result;
};
