export const complaintsCreator = (
  type,
  woundArea,
  woundSide,
  abscessType,
  abscessArea,
  abscessSide,
  neoplasmPoignancy,
  neoplasmArea,
  neoplasmSide,
  sinusitisType,
  sinusitisSide,
  paratonsilarAbscessSide,
  hearingLossSide,
  age
) => {
  const adenoidsTemplate = `   ,   .`;
  const neoplasmTemplate = `   ${neoplasmPoignancy.slice(
    0,
    -2
  )}  ${neoplasmArea} ${neoplasmSide}.`;
  const abscessAreaMod = `${abscessArea} ${abscessSide}`;

  const abscessTemplate =
    abscessType === ""
      ? `     ${abscessAreaMod},  ,   . `
      : `   ${abscessAreaMod},  ,   .`;
  const skinWoundTemplate = `  ${woundArea} ${woundSide}.`;
  const acuteOtitisTemplate = `  ,  ,   .`;
  const noseFractureTemplate = `   ,   .`;
  const septumCurvatureTemplate = `   .`;
  const sinusitisTemplate =
    sinusitisType === "maxillaSinusitis"
      ? `   ,   ,      
      ${sinusitisSide !== `` ? sinusitisSide : "  "}.`
      : "   ,      ,     .";
  const tonsilitisTemplate = `     ,    ,   ,     .`;
  const paratonsilarAbscessTemplate = `    ,   ${paratonsilarAbscessSide},   .`;
  const hlSide =
    hearingLossSide === ""
      ? " "
      : hearingLossSide === ""
      ? ""
      : "";
  const hearingLossTemplate = `  ${hlSide} , ${
    age > 4 ? "  , " : ""
  }  . `;
  switch (type) {
    case "adenoids":
      return adenoidsTemplate;
    case "skinWound":
      return skinWoundTemplate;
    case "abscess":
      return abscessTemplate;
    case "neoplasm":
      return neoplasmTemplate;
    case "acuteOtitis":
    case "mastoiditis":
      return acuteOtitisTemplate;
    case "sinusitis":
      return sinusitisTemplate;
    case "noseFracture":
      return noseFractureTemplate;
    case "septumCurvature":
      return septumCurvatureTemplate;
    case "tonsilitis":
      return tonsilitisTemplate;
    case "hearingLoss":
      return hearingLossTemplate;
    case "paratonsilarAbscess":
      return paratonsilarAbscessTemplate;
    default:
      return ".";
  }
};
